"use client"
import TextGenerator from "@/components/TextGenerator";
import  router from "next/router";
const DottedText = () => {

//   const router = useRouter();
  const handleClick = () => {
    router.push("/metroUI");
  };

  return (
    <div className=" flex flex-col gap-4 bg-black w-screen h-screen p-50">
      <TextGenerator data="welcome" size={"large"} />
      <div
        className=" p-3 bg-stone-800 cursor-pointer"
        onClick={() => handleClick()}
      >
        <TextGenerator data="explore" size={"small"} />
      </div>
    </div>
  );
};
export default DottedText;
